.divAutoComplete {
  position: flex;
  margin: auto;
  margin-top: 1em;
  width: 350px;
  padding: 15px;
  border-radius: 5px;
  max-height: 550px;
  background-color: #282c34;
}

.textAutoComplete {
  width: 100%;
  font-size: 19px;
  padding: 5px;
  font-family: calibri light;
  margin-bottom: 10px;
  border-radius: 3px;
}

.divSuggestions{
    width: 100%;
    background: #FFD32E;
    width: 360px;
    border-radius: 0 0 5px 5px;
    padding: 10px;
}

.divAutoComplete ul {
    margin: 0 auto;
    padding: 0;
    max-height: 390px;
    overflow-y: auto;
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 5px 5px 0 5px;
    border-left: none;
    border-right: none;
}

.divAutoComplete li {
    list-style: none;
    color: white;
    background-color: rgba(0, 0, 0, 0.05);
    background-image:
        linear-gradient(90deg,
        #FFD32E 10px,
        #EEE 10px,
        #EEE 11px,
        transparent 11px);
    padding: 10px 15px 10px 25px;
    border: 1px solid #CCC;
    margin-bottom: 5px;
    width: 100%;
    box-sizing: border-box;
    cursor: pointer;
    border-radius: 3px;
}

