.date {
  color: #111;
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  position: relative;
  text-align: center;
  z-index: 1;
}

.date:before {
  border-top: 1px solid #111;
  content: "";
  position: absolute;
  top: 12px;
  left: 0;
  width: 100%;
  z-index: -1;
}

.author {
  color: #111;
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  padding-bottom: 38px;
  position: relative;
  text-align: center;
  z-index: 1;
}

.author:before {
  border-top: 1px solid #111;
  content: "";
  position: absolute;
  top: 12px;
  left: 0;
  width: 100%;
  z-index: -1;
}

.date span,
.author span {
  background: #fdfdfd;
  padding: 0 10px;
  text-transform: uppercase;
}

.line {
  border-top: 1px solid #111;
  display: block;
  margin-top: 60px;
  padding-top: 50px;
  position: relative;
}

.read-more {
  -moz-border-radius: 50%;
  -moz-transition: all 0.2s ease-in-out;
  -webkit-border-radius: 50%;
  -webkit-transition: all 0.2s ease-in-out;
  background: #111;
  border-radius: 50%;
  border: 10px solid #fdfdfd;
  color: #fff;
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  height: 80px;
  line-height: 80px;
  margin: -40px 0 0 -40px;
  position: absolute;
  bottom: 0px;
  left: 50%;
  text-align: center;
  text-transform: uppercase;
  width: 80px;
}

.read-more:hover {
  background: #990000;
  text-decoration: none;
}

